<template>
  <section class="step-3">
    <div class="add-place-des">
      <h4>Location Details</h4>
    </div>
    <ValidationObserver v-slot="{ invalid }">
      <form @submit.prevent="">
        <ValidationProvider
          name="placeType"
          rules="required"
          v-slot="{ errors }"
        >
          <label for="">Place Type</label>
          <FormulateInput type="radio" label="label" />

          <div class="form-input-wrapper">
            <div class="form-input-element form-input-element--radio">
              <input type="radio" id="apartment" value="1" v-model="placeType" />
              <label for="one">Apartment</label>
            </div>
            
            
          </div>

          <!-- <input type="radio" id="hotelRoom" value="2" v-model="placeType" />
          <label for="two">Hotel Room</label>
          
          <input type="radio" id="house" value="3" v-model="placeType" />
          <label for="house">House</label> -->

          <span>{{ errors[0] }}</span>
        </ValidationProvider>

        <Navigation :valid="!invalid" />
      </form>
    </ValidationObserver>
  </section>
</template>

<script>
import Navigation from "./Navigation.vue";
import { ValidationObserver, ValidationProvider, extend } from "vee-validate";

extend("required", {
  validate(value) {
    return {
      required: true,
      valid: ["", " ", null, undefined].indexOf(value) === -1,
      message: "This field is required",
    };
  },
  computesRequired: true,
});

export default {
  components: { Navigation, ValidationObserver, ValidationProvider, extend },
  data() {
    return {};
  },
  computed: {
    placeType: {
      get() {
        return this.$store.state.steps.step3.placeType;
      },
      set(value) {
        this.$store.commit("setPlaceType", { placeType: value });
      },
    },
  },
  methods: {},
};
</script>

<style scoped lang="scss">
span {
  display: block;
}
</style>
